import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { QuestionBank } from '@app/shared/models/question-bank';

@Component({
  selector: 'app-manage-board',
  templateUrl: './manage-board.component.html',
  styleUrls: ['./manage-board.component.css']
})
export class ManageBoardComponent implements OnInit {

  public questionbank: QuestionBank[];

  public currentRouteModule: string;

  constructor(private activeRoute: ActivatedRoute, private router: Router) {
    this.router.routeReuseStrategy.shouldReuseRoute = () => false;
  }

  ngOnInit() {
    this.questionbank = this.activeRoute.snapshot.data["questionBank"];
    this.currentRouteModule = this.activeRoute.snapshot.paramMap.get("module-name");
  }

  goToQuestion(data) {
    if (data.questionType == 'Essay Structure & Coherence' || data.questionType == 'Essay Comment' || data.questionType == 'Summary Comment')
      this.router.navigate([data.routeUrl], { relativeTo: this.activeRoute });

    else
      this.router.navigate([data.moduleType, data.questionType], { relativeTo: this.activeRoute });
  }
}
